import { _decorator, Node } from 'cc';
const { ccclass } = _decorator;

@ccclass('MatchChecker')
export class MatchChecker {
    public static checkMatch(grid: Node[][], piece: Node, row: number, col: number): boolean {
        return this.checkHorizontalMatch(grid, piece, row, col) || this.checkVerticalMatch(grid, piece, row, col);
    }

    private static checkHorizontalMatch(grid: Node[][], piece: Node, row: number, col: number): boolean {
        const pieceName = piece.name;
        let matchCount = 1;
        console.log(pieceName);
        
        // Check left
        for (let i = col - 1; i >= 0; i--) {
            if (grid[row][i].name === pieceName) {
                matchCount++;
            } else {
                break;
            }
        }

        // Check right
        for (let i = col + 1; i < grid.length; i++) {
            if (grid[row][i].name === pieceName) {
                matchCount++;
            } else {
                break;
            }
        }

        return matchCount >= 3;
    }

    private static checkVerticalMatch(grid: Node[][], piece: Node, row: number, col: number): boolean {
        const pieceName = piece.name;
        let matchCount = 1;

        // Check up
        for (let i = row - 1; i >= 0; i--) {
            if (grid[i][col].name === pieceName) {
                matchCount++;
            } else {
                break;
            }
        }

        // Check down
        for (let i = row + 1; i < grid.length; i++) {
            if (grid[i][col].name === pieceName) {
                matchCount++;
            } else {
                break;
            }
        }

        return matchCount >= 3;
    }
}
